.tags-container {
  display: flex;
  flex-wrap: wrap;
}
.tag {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-bottom: 5px;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 700;
  line-height: 19.96px;
  text-align: center;
  color: white;

  .tag-equipment {
    width: 115px;
    height: 25px;
    border-radius: 10px;
    padding: 2px 5px;
    margin-right: 5px;
    margin-bottom: 5px;
    background-color: #ff6060;
  }
}

@media (max-width: 760px) {
  .tags {
    margin-right: 0;
  }
  .tag {
    .tag-equipment {
      width: 84px;
      height: 18px;
      font-size: 10px;
      line-height: 14.26px;
    }
  }
}
